.artplayer-video-player .artplayer-setting {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 90;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  overflow: hidden; }
  .artplayer-video-player .artplayer-setting .artplayer-setting-inner {
    position: absolute;
    top: 0;
    right: -300px;
    bottom: 0;
    width: 300px;
    height: 100%;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.8);
    transition: all 0.2s ease-in-out;
    overflow: auto; }
    .artplayer-video-player .artplayer-setting .artplayer-setting-inner .artplayer-setting-body {
      overflow-y: auto;
      width: 100%;
      height: 100%; }
      .artplayer-video-player .artplayer-setting .artplayer-setting-inner .artplayer-setting-body .art-setting {
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        padding: 10px 15px; }
        .artplayer-video-player .artplayer-setting .artplayer-setting-inner .artplayer-setting-body .art-setting .art-setting-header {
          margin-bottom: 5px; }
  .artplayer-video-player .artplayer-setting .art-setting-radio {
    display: flex; }
    .artplayer-video-player .artplayer-setting .art-setting-radio .art-radio-item {
      flex: 1;
      padding: 0 2px; }
      .artplayer-video-player .artplayer-setting .art-setting-radio .art-radio-item button {
        height: 22px;
        width: 100%;
        border: none;
        outline: none;
        color: #fff;
        background: rgba(255, 255, 255, 0.2);
        border-radius: 2px; }
      .artplayer-video-player .artplayer-setting .art-setting-radio .art-radio-item.current button {
        background-color: #00a1d6; }
  .artplayer-video-player .artplayer-setting .art-setting-range input {
    width: 100%;
    height: 3px;
    outline: none;
    appearance: none;
    background-color: rgba(255, 255, 255, 0.5); }
  .artplayer-video-player .artplayer-setting .art-setting-checkbox input {
    height: 14px;
    width: 14px;
    margin-right: 5px; }

.artplayer-video-player.artplayer-setting-show .artplayer-setting {
  opacity: 1;
  visibility: visible;
  pointer-events: auto; }
  .artplayer-video-player.artplayer-setting-show .artplayer-setting .artplayer-setting-inner {
    right: 0; }
